/* styles.css */
.container {
  display: flex; /* Use flexbox */
  justify-content: space-around; /* Distribute items evenly along the main axis */
  align-items: horizontal; /* Center items vertically */
}

.button, .sub-button {
  background-color: #ccc;
  color: #000;
  border: 1px solid #000;
  padding: 8px 16px;
  margin-right: 8px;
  cursor: pointer;
}

/* Style for selected category buttons */
.button.selected {
  background-color: #000;
  color: #fff;
}

/* Style for selected subcategory buttons */
.sub-button.selected {
  background-color: #444;
  color: #fff;
}

button.active {
  background-color: #007bff; /* Change background to a blue color */
  color: #fff; /* Change text color to white */
  border-color: #007bff; /* Change border color to match background */
}
